
"use client";

import * as React from "react";
import type { Session, Tournament, User, SavedSpeech, PracticeMode } from "@/lib/types";
import { format, isAfter, parseISO } from "date-fns";
import Link from "next/link";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Info, Users, ArrowRight, Calendar, Gavel, Video, Mic, BrainCircuit } from "lucide-react";
import { Badge } from "@/components/ui/badge";
import { cn, getRoleBasedColor } from "@/lib/utils";
import { useNotifications } from "@/contexts/notification-context";
import { Avatar, AvatarFallback } from "@/components/ui/avatar";
import { useRouter } from "next/navigation";
import { useAuth } from "@/contexts/auth-context";

const TOURNAMENTS_STORAGE_KEY = 'work-session-tournaments-list';
const SAVED_SPEECHES_STORAGE_KEY = 'work-session-saved-speeches';

type Hub = 'scheduler' | 'practice';

export default function DashboardPage() {
  const [activeHub, setActiveHub] = React.useState<Hub | null>(null);
  const [tournaments, setTournaments] = React.useState<Tournament[]>([]);
  const [savedSpeeches, setSavedSpeeches] = React.useState<SavedSpeech[]>([]);
  const { sessions, isMounted } = useNotifications();
  const { user, allUsers: users } = useAuth();
  const router = useRouter();

  React.useEffect(() => {
    if (typeof window !== 'undefined') {
        const storedHub = localStorage.getItem('activeHub') as Hub;
        setActiveHub(storedHub || 'scheduler');
      try {
        const storedTournaments = localStorage.getItem(TOURNAMENTS_STORAGE_KEY);
        const parsedTournaments: Tournament[] = storedTournaments ? JSON.parse(storedTournaments) : [];
        const upcomingTournaments = parsedTournaments
          .filter(t => isAfter(parseISO(t.date), new Date()))
          .sort((a, b) => new Date(a.date).getTime() - new Date(b.date).getTime());
        setTournaments(upcomingTournaments);
      } catch (error) {
        console.error("Failed to parse tournaments from localStorage", error);
        setTournaments([]);
      }

      try {
        const storedSpeeches = localStorage.getItem(SAVED_SPEECHES_STORAGE_KEY);
        const parsedSpeeches: SavedSpeech[] = storedSpeeches ? JSON.parse(storedSpeeches) : [];
         if (user) {
            const userSpeeches = parsedSpeeches.filter(s => s.ownerId === user.id || (s.sharedWith || []).includes(user.id));
            setSavedSpeeches(userSpeeches.sort((a, b) => new Date(b.date).getTime() - new Date(a.date).getTime()));
        }
      } catch (error) {
        console.error("Failed to parse speeches from localStorage", error);
        setSavedSpeeches([]);
      }
    }
  }, [user, isMounted]);
  
  const upcomingBookedSessions = React.useMemo(() => {
    if (!user) return [];
    return sessions
      .filter(s => {
        const isParticipant = s.hostId === user.id || s.clientId === user.id || user.role === 'admin';
        return isParticipant && s.status === 'booked' && isAfter(parseISO(s.date), new Date());
      })
      .sort((a, b) => new Date(a.date).getTime() - new Date(b.date).getTime());
  }, [sessions, user]);
  
  if (!isMounted || !activeHub) {
     return <div className="flex items-center justify-center h-96">Loading...</div>;
  }

  const UserAvatar = ({ userId }: { userId: string | null }) => {
    const participant = users.find(u => u.id === userId);
    if (!participant) return null;
    
    const initials = participant.name.split(" ").map(n => n[0]).join("");
    return (
        <Avatar className="h-6 w-6">
            <AvatarFallback className={cn(getRoleBasedColor(participant.role))}>{initials}</AvatarFallback>
        </Avatar>
    );
  };

  const SchedulerDashboard = () => (
    <div className="grid gap-6 lg:grid-cols-2">
        <Card>
          <CardHeader>
            <CardTitle>Upcoming Tournaments</CardTitle>
          </CardHeader>
          <CardContent>
            {tournaments.length > 0 ? (
              <ul className="space-y-4">
                {tournaments.slice(0, 3).map((tournament) => (
                  <li key={tournament.id} className="flex flex-col sm:flex-row items-start sm:items-center justify-between gap-4 p-4 border rounded-lg bg-card hover:bg-muted/50 transition-colors">
                    <div className="flex-1">
                      <p className="font-semibold text-lg">{tournament.name}</p>
                      <p className="text-sm text-muted-foreground">{format(new Date(tournament.date), "EEEE, MMMM d, yyyy")}</p>
                    </div>
                    <div className="flex items-center gap-2 flex-shrink-0">
                       <Button variant="outline" size="sm" asChild>
                        <Link href={`/dashboard/tournaments/${tournament.id}`}>
                          <Info className="mr-2 h-3 w-3" /> Details
                        </Link>
                      </Button>
                    </div>
                  </li>
                ))}
              </ul>
            ) : (
              <div className="flex flex-col items-center justify-center rounded-lg border border-dashed p-12 text-center h-48">
                <Calendar className="h-8 w-8 text-muted-foreground mb-2" />
                <h3 className="text-lg font-semibold font-headline">No Upcoming Tournaments</h3>
                <p className="text-muted-foreground mt-1 text-sm">
                  The tournament schedule is clear.
                </p>
              </div>
            )}
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle>Upcoming Booked Sessions</CardTitle>
          </CardHeader>
          <CardContent>
            {upcomingBookedSessions.length > 0 ? (
              <ul className="space-y-4">
                {upcomingBookedSessions.slice(0, 3).map((session) => (
                  <li key={session.id} className="flex items-center justify-between gap-4 p-4 border rounded-lg bg-card hover:bg-muted/50 transition-colors">
                    <div className="flex-1 space-y-1">
                       <p className="font-semibold">{session.title}</p>
                       <p className="text-sm text-muted-foreground">{format(new Date(session.date), "EEEE, MMMM d")} at {session.time}</p>
                       <div className="flex items-center pt-1">
                          <div className="flex -space-x-2">
                             <UserAvatar userId={session.hostId} />
                             <UserAvatar userId={session.clientId} />
                          </div>
                       </div>
                    </div>
                     <Button variant="outline" size="sm" asChild>
                        <Link href={`/dashboard/sessions/${session.id}`}>
                            Details <ArrowRight className="ml-2 h-3 w-3" />
                        </Link>
                     </Button>
                  </li>
                ))}
              </ul>
            ) : (
               <div className="flex flex-col items-center justify-center rounded-lg border border-dashed p-12 text-center h-48">
                  <Users className="h-8 w-8 text-muted-foreground mb-2" />
                  <h3 className="text-lg font-semibold font-headline">No Upcoming Sessions</h3>
                  <p className="text-muted-foreground mt-1 text-sm">
                    Your schedule is currently empty.
                  </p>
              </div>
            )}
          </CardContent>
        </Card>
      </div>
  );

  const PracticeDashboard = () => (
     <div className="grid gap-6 lg:grid-cols-2">
        <Card>
            <CardHeader>
                <CardTitle>Recent Recordings</CardTitle>
            </CardHeader>
            <CardContent>
                {savedSpeeches.length > 0 ? (
                <ul className="space-y-4">
                    {savedSpeeches.slice(0, 3).map((speech) => (
                    <li key={speech.id} className="flex items-center justify-between gap-4 p-4 border rounded-lg bg-card hover:bg-muted/50 transition-colors">
                        <div className="flex-1 space-y-1">
                            <p className="font-semibold line-clamp-1">{speech.topic}</p>
                            <p className="text-sm text-muted-foreground">{format(new Date(speech.date), "MMMM d, yyyy")}</p>
                             <Badge variant="secondary" className="capitalize text-xs">{speech.mode}</Badge>
                        </div>
                        <Button variant="outline" size="sm" onClick={() => router.push('/dashboard/extemp-practice')}>
                            View <ArrowRight className="ml-2 h-3 w-3" />
                        </Button>
                    </li>
                    ))}
                </ul>
                ) : (
                <div className="flex flex-col items-center justify-center rounded-lg border border-dashed p-12 text-center h-48">
                    <Video className="h-8 w-8 text-muted-foreground mb-2" />
                    <h3 className="text-lg font-semibold font-headline">No Recent Recordings</h3>
                    <p className="text-muted-foreground mt-1 text-sm">
                        Go to a practice page to record a speech.
                    </p>
                </div>
                )}
            </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle>Recent Practice Rounds</CardTitle>
          </CardHeader>
          <CardContent>
             <div className="flex flex-col items-center justify-center rounded-lg border border-dashed p-12 text-center h-48">
                <Gavel className="h-8 w-8 text-muted-foreground mb-2" />
                <h3 className="text-lg font-semibold font-headline">Coming Soon</h3>
                <p className="text-muted-foreground mt-1 text-sm">
                    Practice rounds will be available here shortly.
                </p>
              </div>
          </CardContent>
        </Card>
      </div>
  );

  return (
    <div className="flex flex-col gap-6">
      <div>
        <h1 className="text-3xl font-bold font-headline">Dashboard</h1>
        <p className="text-muted-foreground">
          An overview of your upcoming events and sessions.
        </p>
      </div>

      {activeHub === 'practice' ? <PracticeDashboard /> : <SchedulerDashboard />}

    </div>
  );
}
